<template>
  <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">
        Каталог
      </h1>
      <span class="content__info">
        {{ formatQuantityName(countProducts, 'товар', '', 'а', 'ов') }}
      </span>
    </div>

    <div class="content__catalog">
      <ProductFilter v-bind.sync="filter" />

      <section class="catalog">
        <SpinnerBase v-if="productsLoading" />
        <div v-if="productsLoadingFailed"> Произошла ошибка: {{ productsLoadingFailed }} <button
            @click.prevent="loadProducts">Попробовать ещё раз</button></div>

        <ProductList :products="products" />
        <Pagination v-model="page" :count="countProducts" :per-page="productsPerPage" />
      </section>
    </div>
  </main>
</template>

<script>
import { formatQuantityName } from '@/helpers/formating';
import ProductList from '@/components/ProductList.vue';
import Pagination from '@/components/base/PaginationBase.vue';
import ProductFilter from '@/components/ProductFilter.vue';
import axios from 'axios';
import { API } from '@/config';
import SpinnerBase from '@/components/base/SpinnerBase.vue';

export default {
  components: {
    ProductList, Pagination, ProductFilter, SpinnerBase,
  },
  data() {
    return {
      page: 1,
      productsPerPage: 3,
      filter: {
        priceFrom: 0,
        priceTo: 0,
        categoryId: 0,
        colorId: 0,
      },
      productsData: null,
      productsLoading: false,
      productsLoadingFailed: false,
    };
  },
  computed: {
    products() {
      return this.productsData?.items.map((p) => ({
        ...p,
        image: p.image.file.url,
      }));
    },
    countProducts() {
      return this.productsData?.pagination?.total;
    },
  },
  methods: {
    formatQuantityName,
    loadProducts() {
      this.productsLoading = true;
      this.productsLoadingFailed = false;
      return axios.get(`${API}/products`, {
        params: {
          page: this.page,
          limit: this.productsPerPage,
          categoryId: this.filter.categoryId || undefined,
          colorId: this.filter.colorId || undefined,
          minPrice: this.filter.priceFrom || undefined,
          maxPrice: this.filter.priceTo || undefined,
        },
      }).then((res) => { this.productsData = res.data; })
        .catch((err) => { this.productsLoadingFailed = err.message; })
        .finally(() => { this.productsLoading = false; });
    },
  },
  watch: {
    page() { this.loadProducts(); },
    filter: { deep: true, immediate: true, handler() { this.loadProducts(); } },
  },
  created() {
    this.filter.categoryId = this.$route.query.categoryId || 0;
  },
};
</script>
