<!-- eslint-disable vuejs-accessibility/label-has-for -->
<template>
  <aside class="filter">
    <h2 class="filter__title">Фильтры</h2>

    <form class="filter__form form" action="#" method="get" @submit.prevent="submit()">
      <fieldset class="form__block">
        <legend class="form__legend">Цена</legend>
        <label class="form__label form__label--price">
          <input class="form__input" type="number" name="min-price" v-model.number="currentPriceFrom">
          <span class="form__value">От</span>
        </label>
        <label class="form__label form__label--price">
          <input class="form__input" type="number" name="max-price" v-model.number="currentPriceTo">
          <span class="form__value">До</span>
        </label>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">Категория</legend>
        <label class="form__label form__label--select">
          <select class="form__select" type="text" name="category" v-model="currentCategoryId">
            <option value="0">Все категории</option>
            <option v-for="cat in categories" :value="cat.id" :key="cat.id">{{ cat.title }}</option>
          </select>
        </label>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">Цвет</legend>
        <ul class="colors">
          <li class="colors__item">
            <label class="colors__label">
              <input class="colors__radio sr-only" type="radio" name="color" value="0" v-model="currentColorId">
              <span class="colors__value" style="background-color: #fff;" title="Все цвета">
                <svg width="100%" height="100%">
                  <use xlink:href="#icon-close"></use>
                </svg>
              </span>
            </label>
          </li>
          <li class="colors__item" v-for="col in colors" :key="col.id">
            <label class="colors__label">
              <input class="colors__radio sr-only" type="radio" name="color" :value="col.id" v-model="currentColorId">
              <span class="colors__value" :style="{'background-color': col.code }" :title="col.title">
              </span>
            </label>
          </li>
        </ul>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">Объемб в ГБ</legend>
        <ul class="check-list">
          <li class="check-list__item">
            <label class="check-list__label">
              <input class="check-list__check sr-only" type="checkbox" name="volume" value="8" checked="">
              <span class="check-list__desc">
                8
                <span>(313)</span>
              </span>
            </label>
          </li>
          <li class="check-list__item">
            <label class="check-list__label">
              <input class="check-list__check sr-only" type="checkbox" name="volume" value="16">
              <span class="check-list__desc">
                16
                <span>(461)</span>
              </span>
            </label>
          </li>
          <li class="check-list__item">
            <label class="check-list__label">
              <input class="check-list__check sr-only" type="checkbox" name="volume" value="32">
              <span class="check-list__desc">
                32
                <span>(313)</span>
              </span>
            </label>
          </li>
          <li class="check-list__item">
            <label class="check-list__label">
              <input class="check-list__check sr-only" type="checkbox" name="volume" value="64">
              <span class="check-list__desc">
                64
                <span>(313)</span>
              </span>
            </label>
          </li>
          <li class="check-list__item">
            <label class="check-list__label">
              <input class="check-list__check sr-only" type="checkbox" name="volume" value="128">
              <span class="check-list__desc">
                128
                <span>(313)</span>
              </span>
            </label>
          </li>
          <li class="check-list__item">
            <label class="check-list__label">
              <input class="check-list__check sr-only" type="checkbox" name="volume" value="264">
              <span class="check-list__desc">
                264
                <span>(313)</span>
              </span>
            </label>
          </li>
        </ul>
      </fieldset>

      <button class="filter__submit button button--primery" type="submit">
        Применить
      </button>
      <button class="filter__reset button button--second" type="button" @click.prevent="submit(true)">
        Сбросить
      </button>
    </form>
  </aside>
</template>

<script>
import { API } from '@/config';
import axios from 'axios';

export default {
  props: ['priceFrom', 'priceTo', 'categoryId', 'colorId'],
  data() {
    return {
      currentPriceFrom: 0,
      currentPriceTo: 0,
      currentCategoryId: 0,
      currentColorId: 0,

      categoriesData: null,
      colorsData: null,
    };
  },
  computed: {
    categories() { return this.categoriesData?.items; },
    colors() { return this.colorsData?.items; },
  },
  watch: {
    priceFrom(value) { this.currentPriceFrom = value; },
    priceTo(value) { this.currentPriceTo = value; },
    categoryId: { immediate: true, handler(value) { this.currentCategoryId = value; } },
    colorId(value) { this.currentColorId = value; },
  },
  methods: {
    submit(reset = false) {
      this.$emit('update:priceFrom', (reset) ? 0 : this.currentPriceFrom);
      this.$emit('update:priceTo', (reset) ? 0 : this.currentPriceTo);
      this.$emit('update:categoryId', (reset) ? 0 : this.currentCategoryId);
      this.$emit('update:colorId', (reset) ? 0 : this.currentColorId);
    },
    loadCategories() {
      return axios.get(`${API}/productCategories`).then((r) => { this.categoriesData = r.data; });
    },
    loadColors() {
      return axios.get(`${API}/colors`).then((r) => { this.colorsData = r.data; });
    },
  },
  created() {
    this.loadCategories();
    this.loadColors();
  },
};
</script>
