<!-- eslint-disable vuejs-accessibility/label-has-for -->
<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'cart' }">
            Корзина
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info">
        {{ formatQuantityName(productsLength, 'товар', '', 'а', 'ов') }}
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST" @submit.prevent="ordering">
        <div class="cart__field">
          <FieldsBase v-model="formData" :formError="formError" />

          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="0" checked="">
                  <span class="options__value">
                    Самовывоз <b>бесплатно</b>
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="500">
                  <span class="options__value">
                    Курьером <b>500 ₽</b>
                  </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="card">
                  <span class="options__value">
                    Картой при получении
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="cash">
                  <span class="options__value">
                    Наличными при получении
                  </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <div class="cart__block">
          <ul class="cart__orders">
            <OrderItem v-for="item in products" :key="item.productId" :item="item" />
          </ul>

          <div class="cart__total">
            <p>Доставка: <b>самовывоз</b></p>
            <p>Итого: <b>{{ productsLength }}</b> {{ formatQuantityName(productsLength, 'товар', '', 'а', 'ов', false) }}
              на сумму <b>{{ totalPrice | formatPrice }}</b>
            </p>
          </div>

          <button v-show="productsLength > 0" class="cart__button button button--primery" type="submit"
            :disabled="orderSending">
            <SpinnerBase v-if="orderSending" />
            <span v-else>Оформить заказ</span>
          </button>
        </div>
        <div class="cart__error form__error-block" v-show="formErrorMessage">
          <h4>Заявка не отправлена!</h4>
          <p>
            {{ formErrorMessage }}
          </p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import FieldsBase from '@/components/base/FieldsBase.vue';
import { formatPrice, formatQuantityName } from '@/helpers/formating';
import { mapGetters } from 'vuex';
import OrderItem from '@/components/OrderItem.vue';
import axios from 'axios';
import { API } from '@/config';
import SpinnerBase from '@/components/base/SpinnerBase.vue';

export default {
  name: 'OrderPage',
  components: { FieldsBase, OrderItem, SpinnerBase },
  data() {
    return {
      formData: {},
      formError: {},
      formErrorMessage: '',
      orderSending: false,
    };
  },
  filters: { formatPrice },
  computed: {
    ...mapGetters({ products: 'cartDetailProducts', totalPrice: 'cartTotalPrice' }),
    productsLength() { return this.products.length; },
  },
  methods: {
    formatQuantityName,
    ordering() {
      this.formError = {};
      this.formErrorMessage = '';
      this.orderSending = true;

      return axios.post(`${API}/orders`, { ...this.formData }, { params: { userAccessKey: this.$store.state.userAccessKey } })
        .then((resp) => {
          this.$store.commit('resetCart');
          this.$store.commit('updateOrderInfo', resp.data);
          this.$router.push({ name: 'orderInfo', params: { id: resp.data.id } });
        }).catch((err) => {
          this.formError = err.response.data.error.request || {};
          this.formErrorMessage = err.response.data.error.message || '';
        }).finally(() => { this.orderSending = false; });
    },
  },
};
</script>
