<!-- eslint-disable vuejs-accessibility/label-has-for -->
<!-- eslint-disable vuejs-accessibility/form-control-has-label -->
<template>
  <SpinnerBase v-if="productLoading" />
  <div v-else-if="productLoadingFailed"> Произошла ошибка: {{ productLoadingFailed }} <button
      @click.prevent="loadProduct">Попробовать ещё раз</button></div>

  <main class="content container" v-else>
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'main', query: { categoryId: category.id } }">
            {{ category.title }}
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            {{ product.title }}
          </a>
        </li>
      </ul>
    </div>

    <section class="item">
      <div class="item__pics pics">
        <div class="pics__wrapper">
          <img width="570" height="570" :src="product.image" :alt="product.title">
        </div>
        <ul class="pics__list">
        </ul>
      </div>

      <div class="item__info">
        <span class="item__code">Артикул: {{ product.id }}</span>
        <h2 class="item__title">
          {{ product.title }}
        </h2>
        <div class="item__form">
          <form class="form" action="#" method="POST" @submit.prevent="addToCart">
            <b class="item__price">
              {{ product.price | formatPrice }}
            </b>

            <fieldset class="form__block" v-if="product.colors.length > 0">
              <legend class="form__legend">Цвет:</legend>
              <ProductColors :productColors="product.colors" v-model="currentProductColorId"
                :radio-group="product.id" />
            </fieldset>

            <div class="item__row">
              <CounterBase v-model.number="productAmount" maxValue="99" />

              <button class="button button--primery" type="submit" :disabled="productSending">
                <span v-if="!productSending">В корзину</span>
                <SpinnerBase v-if="productSending" />
              </button>
            </div>
            <div v-show="productAdded">
              <p>Товар добавлен в корзину</p>
            </div>
          </form>
        </div>
      </div>

      <div class="item__desc">
        <ul class="tabs">
          <li class="tabs__item">
            <a class="tabs__link tabs__link--current">
              Описание
            </a>
          </li>
          <li class="tabs__item">
            <a class="tabs__link" href="#">
              Характеристики
            </a>
          </li>
          <li class="tabs__item">
            <a class="tabs__link" href="#">
              Гарантия
            </a>
          </li>
          <li class="tabs__item">
            <a class="tabs__link" href="#">
              Оплата и доставка
            </a>
          </li>
        </ul>

      </div>
    </section>
  </main>
</template>

<script>
/* eslint-disable func-names */
import { formatPrice } from '@/helpers/formating';
import CounterBase from '@/components/base/CounterBase.vue';
import ProductColors from '@/components/ProductColors.vue';
import axios from 'axios';
import { API } from '@/config';
import SpinnerBase from '@/components/base/SpinnerBase.vue';
import { mapActions } from 'vuex';

export default {
  data() {
    return {
      productAmount: 1,
      productColorId: null,

      productData: null,
      productLoading: false,
      productLoadingFailed: false,

      productSending: false,
      productAdded: false,
    };
  },
  filters: { formatPrice },
  computed: {
    product() {
      return { ...this.productData, image: this.productData.image.file.url };
    },
    category() {
      return this.productData.category;
    },
    currentProductColorId: {
      get() { return this.productColorId || this.$route.query.colorId || this.product.colors[0].id; },
      set(value) { this.productColorId = value; },
    },
  },
  methods: {
    ...mapActions(['addProductToCart']),
    addToCart() {
      if (this.productAmount < 1) return;
      this.productAdded = false;
      this.productSending = true;

      this.addProductToCart(
        { productId: this.product.id, amount: this.productAmount },
      ).then(() => {
        this.productAdded = true;
        this.productSending = false;
      });
    },
    loadProduct() {
      this.productLoading = true;
      this.productLoadingFailed = false;
      return axios.get(`${API}/products/${this.$route.params.id}`)
        .then((res) => { this.productData = res.data; })
        .catch((err) => { this.productLoadingFailed = err.message; })
        .finally(() => { this.productLoading = false; });
    },
  },
  components: { CounterBase, ProductColors, SpinnerBase },
  watch: {
    '$route.params.id': { handler() { this.loadProduct(); }, immediate: true },
  },
};
</script>
