<script>
import CartIndicator from '@/components/CartIndicator.vue';
import { mapActions, mapMutations } from 'vuex';
import SpinnerBase from './components/base/SpinnerBase.vue';

export default {
  name: 'App',
  components: { CartIndicator, SpinnerBase },
  data() { return { cartLoading: false }; },
  methods: { ...mapActions(['loadCart']), ...mapMutations(['updateUserAccessKey']) },
  created() {
    this.cartLoading = true;
    const userAccessKey = localStorage.getItem('userAccessKey');
    if (userAccessKey) this.updateUserAccessKey(userAccessKey);
    this.loadCart().finally(() => { this.cartLoading = false; });
  },
};
